*
{
    margin: 0;
    padding: 0;
}
.container
{
    width: 100%;
    height: 100vh;
    background: linear-gradient(219deg,#00caff 0%, #e4ff00 80%);
    color: rgb(0, 41, 77);
}
.content
{
    width: 100%;
    height: 100vh;  
    display: flex;
    align-items: center;
    justify-content: center;
}
.reg
{
    width: 350px;
    border: 2px solid black;
    border-radius: 25px;
    padding: 15px;
}
.reg h1{
    margin-bottom: 10px;
    font-size: 25px;
    text-align: center;
    text-decoration: underline;
}
.reg input
{
    width: 300px;
    height: 30px;
    padding: 5px;
    background: transparent;
    border-bottom: 1px solid black;
    outline: none;
    margin: 5px;
}
label {
    margin: 3px;
    font-weight: bold;
}
.reg button
{
    width:150px;
    height: 30px;
    margin-top: 25px;
    margin-right: 25px;
    cursor: pointer;
    font-size: 15px;
    font-weight: bold;
    color: white;
    padding: 5px;
    border: none;
    outline: none;
    background-color: darkgreen;
    border-radius: 25px;
}
span
{
    color: red;
    font-size: 15px;
}